body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", sans-serif, "google";
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
body button, body input[type=submit], body input[type=reset] {
  background: none;
  color: inherit;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

.entete {
  width: 100%;
  height: auto;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
nav {
  position: relative;
  background: #ccc;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  z-index: 12;
}
@media screen and (max-width: 768px) {
  nav {
    justify-content: left;
  }
}

.navMenu {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .navMenu {
    position: absolute;
    top: 50px;
    left: 1rem;
    display: flex;
    flex-direction: column;
    align-items: left;
    background: #ccc;
    animation: 0.5s move ease-out;
    animation-fill-mode: both;
  }
}

@media screen and (max-width: 768px) {
  .navButton {
    width: 100%;
    border-bottom: 1px solid rgba(33, 33, 33, 0.1);
  }
}
.navButton:hover {
  background: #03A5AB;
}

.navLink {
  color: black;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 14.5px 15px;
  height: 100%;
  cursor: pointer;
}
.navLink:focus {
  background: #03A5AB;
}
.navLink.active {
  background: #03A5AB;
}

.hamburger {
  display: none;
}
@media screen and (max-width: 768px) {
  .hamburger {
    display: block;
    height: 25px;
    width: 30px;
    margin-left: 1rem;
  }
}

.threeBars {
  position: relative;
}
.threeBars .bar {
  position: absolute;
  top: 0;
  left: 0;
  width: 30px;
  height: 3px;
  background: #000;
  transition: all 0.3s ease;
}
.threeBars .bar:nth-child(1) {
  transform: translateY(-8px) rotate(0deg);
}
.threeBars .bar:nth-child(3) {
  transform: translateY(8px) rotate(0deg);
}
.threeBars.arrow .bar:nth-child(1) {
  transform-origin: left center;
  transform: translateY(0px) rotate(405deg);
  width: 12px;
}
.threeBars.arrow .bar:nth-child(3) {
  transform-origin: left center;
  transform: translateY(0px) rotate(315deg);
  width: 12px;
}
.threeBars.arrow .bar:nth-child(2) {
  transform-origin: center center;
  transform: translateY(0%) rotate(180deg);
}

@keyframes comesIn {
  0% {
    left: -20rem;
  }
  25% {
    left: 4rem;
  }
  50% {
    left: 0.5rem;
  }
  75% {
    left: 1.25rem;
  }
  100% {
    left: 1rem;
  }
}
@keyframes comesOut {
  0% {
    left: 1rem;
  }
  35% {
    left: 1.5rem;
  }
  100% {
    left: -20rem;
  }
}
.comesIn {
  animation-name: comesIn;
}

.comesOut {
  animation-name: comesOut;
}

